import { gsap } from 'gsap';
import './todo.scss';
import './fontello-8da18240/css/fontello-embedded.css';
import { rootEdits } from './Ts/memoryConversions';
import { newClock, adjustAllFatherHeight } from './Ts/clock';
import displayAll from './Ts/memStuff';
import refresh from './Ts/refresh';
if ('serviceWorker' in navigator) {
    window.addEventListener('load', function () {
        navigator.serviceWorker.register('./serviceWorker.js');
    });
}
setInterval(function () {
    newClock();
}, 1000);
window.addEventListener('resize', adjustAllFatherHeight);
//Display the data
displayAll();
var mainBtns = document.querySelector('.mainBtns');
$('#bottomPiece').click(function () {
    gsap.to(mainBtns, {
        width: '90%',
        height: '10vh',
        duration: 0.25,
        onComplete: function () {
            //Closes of the main buttons after x milliseconds
            setTimeout(function () {
                gsap.to(mainBtns, {
                    width: '30%',
                    height: '0',
                    duration: 0.25,
                });
            }, 2000);
        },
    });
});
//Deleting all done tasks and carets from the root
$('#del-checked').click(function () {
    rootEdits.delChecked();
    refresh();
});
$('#add').click(function () {
    rootEdits.addChild();
});
$('#del-all').click(function () {
    rootEdits.delAll();
    refresh();
});
//Save the scroll position at the end of each scroll
$('.allFather').scroll(function () {
    localStorage.removeItem('scrollPos');
    localStorage.setItem('scrollPos', JSON.stringify($('.allFather').scrollTop()));
});
