import { findAdress } from '../findAddress';
import refresh from '../refresh';
var liClick = function () {
    var inSortModeJSON = sessionStorage.getItem('inSortMode');
    var inSortmode = inSortModeJSON === null ? false : JSON.parse(inSortModeJSON);
    if (!inSortmode) {
        //Checking if the clicked is a task or an options button
        var target = event === null || event === void 0 ? void 0 : event.target;
        var isEventTargetTask = {
            li: target.classList.contains('not-done') ||
                target.classList.contains('done') ||
                target.classList.contains('show-off-task'),
            text: target.classList.contains('task-txt'),
            div: target.classList.contains('task'),
        };
        if (!target.classList.contains('caret') &&
            (isEventTargetTask.li || isEventTargetTask.text || isEventTargetTask.div)) {
            //Getting the proper element
            var clicked = isEventTargetTask.li
                ? target
                : $(target).parents('li')[0];
            // console.log(clicked);
            //Getting the task from the mem
            // console.log(clicked);
            var taskPathAndCheckout = findAdress(clicked);
            taskPathAndCheckout.checkout.forEach(function (n) {
                n.flip();
            });
            //Refreshing the allFather
            refresh();
        }
    }
};
export default liClick;
