var liGenerator = function (task, showOff) {
    if (showOff === void 0) { showOff = false; }
    var li = document.createElement('li');
    var done = task.state() ? 'done' : 'not-done';
    var ID = task.taskID();
    var showing = task.showing()
        ? 'showing'
        : 'not-showing';
    li.setAttribute('data-taskID', ID);
    // console.log(task);
    // If n is a caret
    if (task.isCaret()) {
        // Root caret
        if (showOff) {
            /*
                  <li class="show-off ${done} caret-show-off">
                      <div class="caret ${done} caret-show-off" data-state="${showing}">
                          <span class="task-txt">${n.title}</span>
                          <span class="icon-ellipsis-vert optBtns"></span>
                      </div>
                  </li>
                  */
            li.classList.add('show-off', done, 'caret-show-off');
            li.innerHTML = "<div class=\"caret " + done + " caret-show-off\" data-state=\"" + showing + "\" data-taskID=\"" + ID + "\"><span class=\"task-txt\">" + task.title() + "</span><span class=\"icon-ellipsis-vert optBtns\"></span></div>";
        }
        // Sub task caret
        else {
            /*
                  <li class="${done} li-caret">
                      <div class="caret ${done}" data-state="${show}">
                          <span class="task-txt">${n.title}</span>
                          <span class="icon-ellipsis-vert optBtns"></span>
                      </div>
                  </li>
                  */
            li.classList.add(done, 'li-caret');
            li.innerHTML = "<div class=\"caret " + done + "\" data-state=\"" + showing + "\" data-taskID=\"" + ID + "\"><span class=\"task-txt\">" + task.title() + "</span><span class=\"icon-ellipsis-vert optBtns\"></span></div>";
        }
    }
    // If n is a task
    else {
        // Root task
        if (showOff) {
            /*
              <li class="show-off show-off-task ${done}">
                  <div class="task">
                      <span class="task-txt ${done}">${n.title}</span>
                      <span span class="icon-ellipsis-vert optBtns"></span>
                  </div>
                  <ul class="nested"></ul>
              </li>
              */
            li.classList.add('show-off', 'show-off-task', done);
            li.innerHTML = "<div class=\"task\"><span class=\"task-txt " + done + "\">" + task.title() + "</span><span span class=\"icon-ellipsis-vert optBtns\"></span></div><ul class=\"nested\"></ul>";
        }
        // Sub task task
        else {
            /*
                  <li class="${done}">
                      <div class="task">
                          <span class="task-txt">${n.title}</span>
                          <span class="icon-ellipsis-vert optBtns"></span>
                      </div>
                      <ul class="nested"></ul>
                  </li>
                  */
            li.classList.add(done);
            li.innerHTML = "<div class=\"task\"><span class=\"task-txt\">" + task.title() + "</span><span class=\"icon-ellipsis-vert optBtns\"></span></div><ul class=\"nested\"></ul>";
        }
    }
    return li;
};
export default liGenerator;
