import findTitle from './findTitle';
import { mem } from './memoryConversions';
function findAdress(ev) {
    //Estabrishing address in object form
    var parentsObj = $(ev).parents('.nested').prev('.caret').not('.nested');
    delete parentsObj.prevObject;
    delete parentsObj.length;
    //Estabrishing da adress in Array
    var arr = Object.values(parentsObj).reverse();
    arr.push(ev);
    var adress = [];
    arr.forEach(function (n) {
        var ID = n.getAttribute('data-taskID');
        adress.push(ID !== null ? ID : 'err');
        //Alerting of error
        ID === null && n !== document.querySelector('body')
            ? console.error('Could not get data-taskID attribute', n)
            : {};
    });
    //Getting to the Estabrished adress in the memory
    var checkoutCodon = [];
    var path = mem[findTitle(adress[0], mem)];
    checkoutCodon.push(path);
    for (var i = 1; i < adress.length; i++) {
        if (path.isCaret()) {
            var arr_1 = path.children();
            var titl = adress[i];
            var indx = findTitle(titl, arr_1);
            var newPath = arr_1[indx];
            path = newPath;
            checkoutCodon.push(path);
        }
    }
    //li caret checkout
    var checkout = checkoutCodon.reverse();
    return { path: path, checkout: checkout };
}
export { findAdress };
