import liGenerator from '../taskHTML';
//Used to create .nested elements
var displayChillun = function (ul, children) {
    //Setting up new ul element
    var newUl = document.createElement('ul');
    newUl.classList.add('nested');
    //<ul class="nested" />
    children.forEach(function (n) {
        //Setting up the done class
        var done = n.state() ? 'done' : 'not-done';
        //# If n is a caret
        if (n.isCaret()) {
            //Setting up the data-state
            var show = n.showing() ? 'showing' : 'not-showing';
            var li = liGenerator(n);
            displayChillun(li, n.children());
            //Appending the div to the newUl
            newUl.append(li);
        }
        //# If n is just a task
        else {
            var li = liGenerator(n);
            // displayChillun(li, []);
            //Appending the new li to the newUl
            newUl.append(li);
        }
    });
    $(ul).append(newUl);
};
export default displayChillun;
