import tippy, { sticky } from 'tippy.js';
import { gsap } from 'gsap';
import { findAdress } from './findAddress';
import refresh from './refresh';
import { clearSortInsts } from './sortableStuff';
import { rootEdits } from './memoryConversions';
import { mem } from './memoryConversions';
import { swalConfirm, swalNotification } from './alerts';
var openTippy = function () {
    //checking if there's an open tippy
    var thereIsAOpenTippyJSON = sessionStorage.getItem('ThersOpenTippy');
    var thereIsAOpenTippy = thereIsAOpenTippyJSON !== null ? JSON.parse(thereIsAOpenTippyJSON) : false;
    //Openig the tippy only if there's no other tippy opoen
    if (!thereIsAOpenTippy && event !== undefined) {
        clearSortInsts();
        var target = event.target;
        // debugger;
        var Li_1 = target.parentElement.parentElement;
        var isACaretShowOff = $(Li_1).hasClass('caret-show-off') || $(Li_1).hasClass('li-caret');
        var child = $(Li_1);
        // * Setting transition speed for GSAP animation and setting default attributes for tippy
        var transitionTime_1 = 0.25;
        var padding = parseFloat(child.css('padding-top'));
        var offsetY = padding +
            (isACaretShowOff
                ? parseFloat($(Li_1.firstElementChild).css('padding-top'))
                : 0);
        //targetAsElement is just so TS can shut up
        var targetAsElement = event.target;
        var editOPts_1 = tippy(targetAsElement, {
            zIndex: 999,
            theme: 'todTheme',
            arrow: false,
            allowHTML: true,
            placement: 'top',
            sticky: true,
            trigger: 'click',
            interactive: true,
            appendTo: document.querySelector('body'),
            offset: [0, offsetY],
            plugins: [sticky],
            onShow: function (inst) {
                sessionStorage.setItem('ThersOpenTippy', 'true');
            },
        });
        //* Finding the clicked element's address
        var LiFirstParent = target.parentElement;
        var accessedTod_1 = findAdress(Li_1);
        console.log(Li_1);
        //* Increase the height of the nested
        var InitalMarginTop_1 = parseFloat(child.css('margin-top'));
        editOPts_1.setProps({
            // Setting the right buttons for editing
            content: "\n        <h1>\n          <i class=\"icon-pencil\"></i>\n          " + (accessedTod_1.path.isCaret()
                ? '<i class="icon-minus-squared"></i>'
                : '') + "\n          <i class=\"icon-plus\"></i>\n          " + (accessedTod_1.path.isCaret()
                ? '<i class="icon-minus-squared-alt"></i>'
                : '') + "\n          <i class=\"icon-trash-empty\"></i>\n        </h1>\n      ",
            placement: 'top-end',
            animateFill: true,
            hideOnClick: false,
            onClickOutside: function (inst, ev) {
                // console.log("out side click");
                editOPts_1.hide();
                gsap.to(Li_1, {
                    marginTop: InitalMarginTop_1 + "px",
                    duration: transitionTime_1,
                    onComplete: function () {
                        editOPts_1.destroy();
                        refresh();
                    },
                });
            },
        });
        editOPts_1.show();
        var childMarginTop = parseFloat(child.css('margin-top'));
        var tippyHeight = parseFloat($('.tippy-box').css('height'));
        //* Setting the css
        var borderWidth = parseFloat(child.css('border-top-width'));
        $('.tippy-box')
            .css('background-color', child.css('background-color'))
            .css('border-width', borderWidth + "px " + borderWidth + "px 0px " + borderWidth + "px")
            .css('border-color', "" + child.css('border-top-color'));
        $('.tippy-box .tippy-content h1 i').css('color', child.css('color'));
        $('.tippy-box .tippy-content h1 .icon-trash-empty').css('color', '#f57a62');
        //* Setting up the events for the editing of tasks
        var editorBtns_1 = {
            editTitle: function () {
                var rootSiblings = accessedTod_1.checkout[1] === undefined;
                accessedTod_1.path.editTitle(rootSiblings ? mem : accessedTod_1.checkout[1].children(), function () {
                    editOPts_1.hide();
                    editOPts_1.destroy();
                    refresh();
                });
            },
            addChild: function () {
                //Adding newtask to memory
                accessedTod_1.path.addChild(function () {
                    //Flipping all parents state's
                    accessedTod_1.checkout.forEach(function (n) {
                        n.flip();
                    });
                    editOPts_1.destroy();
                });
            },
            delete: function () {
                swalConfirm({
                    title: 'Task deletion',
                    focus: 'cancel',
                    message: "Are you sure you wan't to delete this task",
                    success: function () {
                        // Deleting from the root array
                        if (accessedTod_1.checkout.length == 1) {
                            rootEdits.delChild(accessedTod_1.path.taskID());
                        }
                        // Deleting a sub child
                        else {
                            //Getting the parent
                            var parent_1 = accessedTod_1.checkout[1];
                            //Deleting selected task/caret from it's parent
                            parent_1.deleteChild(accessedTod_1.path.taskID());
                            //Flipping all parents state's
                            accessedTod_1.checkout.forEach(function (n) {
                                if (n.children().length != 0) {
                                    n.flip();
                                }
                            });
                        }
                        editOPts_1.destroy();
                        refresh();
                    },
                });
            },
            delAllDone: function () {
                var allDones = accessedTod_1.path.children().filter(function (n) { return n.state(); });
                //if there are any done tasks
                if (allDones.length > 0) {
                    //firing confirmation prompt
                    var confirmation = confirm('Are You Sure? All done tasks and carets will be deleted');
                    //If there users wants to continue...
                    if (confirmation) {
                        //Delete all done method
                        accessedTod_1.path.delAllDone();
                        //Refreshing the allFather
                        editOPts_1.destroy();
                        refresh();
                    }
                    //If the users is REMORSEFUL for such vile actions
                    else {
                        swalNotification({
                            title: 'Action Cancelled',
                            message: 'There were no changes',
                            icon: 'info',
                        });
                    }
                }
                //If there aren't any done tasks
                else {
                    swalNotification({
                        title: 'There are no done tasks to delete',
                        icon: 'info',
                    });
                }
            },
            delAll: function () {
                //Confirmation prompt fired
                var confirmation = confirm('Are You Sure? Everything will be deleted...');
                //If user wants to continue...
                if (confirmation) {
                    accessedTod_1.path.delAll();
                    editOPts_1.destroy();
                    refresh();
                }
                //If users cancels the action
                else {
                    swalNotification({
                        title: 'Action Cancelled',
                        message: 'There were no changes',
                        icon: 'info',
                    });
                }
            },
        };
        $('.tippy-box .icon-pencil').click(function () {
            editorBtns_1.editTitle();
        });
        $('.tippy-box .icon-minus-squared').click(function () {
            editorBtns_1.delAll();
        });
        $('.tippy-box .icon-plus').click(function () {
            editorBtns_1.addChild();
        });
        $('.tippy-box .icon-trash-empty').click(function () {
            editorBtns_1.delete();
        });
        $('.tippy-box .icon-minus-squared-alt').click(function () {
            editorBtns_1.delAllDone();
        });
        gsap.to(Li_1, {
            marginTop: childMarginTop + tippyHeight + "px",
            duration: 0.25,
        });
    }
};
export default openTippy;
